@import "../../02_Styles/variables.scss";


.contact {
  background-color: $grey-dark;
}
.container{}

.contactForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.nameBlock, .subjectEmailBlock {
  @include breakpoint(1024) {
    display: flex;
    justify-content: space-between;
    }
}

.contactTitle {
  width: 100%;
  font-size: $sectionTitleMobile;
  padding: 20px;
  text-align: center;
  @include breakpoint(480) {
    padding: 30px;
  }
}

.buttons {
  width: 300px;
  display: flex;
  justify-content: space-between;
  @include breakpoint(480) {
    width: 400px;
  }
  & input {
    width: 127px;
    padding: 10px 10px;
    border-radius: $bRadius5;
    cursor: pointer;
    border: none;
    @include breakpoint(480) {
      width: 170px;
    }
    @include breakpoint(768) {
      font-size: 1.2rem;
    }

    &:hover {
      color: $orange;
    }
  }
}
