@import "../../02_Styles/variables.scss";
.slider {
  background-color: $grey-dark;
  min-height: 900px;
  padding-top: 10px;
  display: flex;
  justify-content: center;
  @include breakpoint(480) {
    padding-top: 50px;
  }
  @include breakpoint(768) {
    padding-top: 50px;
  }

}
.container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 90%;
  overflow: hidden;
  cursor: pointer;
  touch-action: pan-x; // Mobile browser touch action
}

.sliderList {
  display: flex;
  min-width: 100%; // If image will be less than container, then it take size of container.
  transition: transform 0.3s ease-out;
  &.swiping {
    transition: none;
  }
}

.sliderItem {
  width: 100%;
  flex-shrink: 0;
}
.sliderImage {
  width: 100%;
  height: auto;
  user-select: none;
}

.sliderIndicator {
  display: flex;
  &Item {
    height: 10px;
    width: 10px;
    border-radius: 50%;
    border: 2px solid $orange;
    margin: 10px 10px 0 0;
    cursor: pointer;
    &.active {
      background-color: $orange;
      border-color: $orange;
    }
    @include breakpoint(768) {
      height: 15px;
      width: 15px;
    }
  }
}

.new {
  position: absolute;
  display: block;
  top: 10px;
  left: 10px;
  text-transform: uppercase;
  padding: 10px 15px;
  background-color: $grey-light;
  z-index: 1;
  @include breakpoint(768) {
    top: 20px;
    left: 20px;
    padding: 15px 20px;
    font-size: 1.5rem;
  }
}
